import React, { useState, useEffect } from 'react';
import {
  Box,
  Grid,
  Card,
  CardContent,
  Typography,
  Button,
  Chip,
  LinearProgress,
  IconButton,
  Tooltip,
  Alert,
  Paper,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Divider,
  Avatar,
  Badge,
  CircularProgress,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TablePagination,
} from '@mui/material';
import {
  Refresh as RefreshIcon,
  PlayArrow as PlayIcon,
  Pause as PauseIcon,
  Stop as StopIcon,
  Settings as SettingsIcon,
  TrendingUp as TrendingUpIcon,
  TrendingDown as TrendingDownIcon,
  NetworkCheck as NetworkCheckIcon,
  Security as SecurityIcon,
  Cloud as CloudIcon,
  Speed as SpeedIcon,
  BugReport as BugReportIcon,
  Storage as StorageIcon,
  Router as RouterIcon,
  Wifi as WifiIcon,
  Memory as MemoryIcon,
  Memory as CpuIcon,
  Storage as StorageIcon2,
  Timeline as TimelineIcon,
  Assessment as AssessmentIcon,
  Warning as WarningIcon,
  CheckCircle as CheckCircleIcon,
  Error as ErrorIcon,
  Info as InfoIcon,
} from '@mui/icons-material';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip as RechartsTooltip, ResponsiveContainer, BarChart, Bar, PieChart, Pie, Cell } from 'recharts';

// Import components
import MetricCard from '../components/MetricCard';
import StatusBadge from '../components/StatusBadge';
import LoadingSpinner from '../components/LoadingSpinner';
import ErrorMessage from '../components/ErrorMessage';

// Mock data for demonstration
const mockData = {
  systemStatus: {
    overall: 'healthy',
    uptime: '2d 14h 32m',
    version: '1.0.0',
    lastUpdate: '2024-01-15 14:30:00',
  },
  protocols: [
    { name: 'BGP', status: 'running', neighbors: 3, routes: 1250, uptime: '2d 14h' },
    { name: 'OSPF', status: 'running', neighbors: 5, routes: 890, uptime: '2d 14h' },
    { name: 'ISIS', status: 'stopped', neighbors: 0, routes: 0, uptime: '0s' },
  ],
  interfaces: [
    { name: 'eth0', status: 'up', speed: '1Gbps', rx: 1250000, tx: 980000, errors: 0 },
    { name: 'eth1', status: 'up', speed: '1Gbps', rx: 890000, tx: 1100000, errors: 0 },
    { name: 'eth2', status: 'down', speed: '10Gbps', rx: 0, tx: 0, errors: 0 },
  ],
  cloudResources: {
    cloudpods: {
      instances: 12,
      networks: 8,
      storages: 15,
      loadbalancers: 3,
      status: 'connected',
    },
    aviatrix: {
      gateways: 5,
      transitGateways: 2,
      spokeGateways: 3,
      status: 'connected',
    },
  },
  trafficStats: {
    totalPackets: 12500000,
    totalBytes: 15600000000,
    packetsPerSecond: 1250,
    bytesPerSecond: 1560000,
    droppedPackets: 125,
    droppedBytes: 156000,
  },
  performanceMetrics: [
    { time: '00:00', cpu: 45, memory: 60, network: 30 },
    { time: '04:00', cpu: 35, memory: 55, network: 25 },
    { time: '08:00', cpu: 65, memory: 70, network: 80 },
    { time: '12:00', cpu: 75, memory: 75, network: 90 },
    { time: '16:00', cpu: 55, memory: 65, network: 60 },
    { time: '20:00', cpu: 40, memory: 58, network: 35 },
  ],
  recentEvents: [
    { time: '14:30:15', type: 'info', message: 'BGP neighbor 192.168.1.2 established', severity: 'info' },
    { time: '14:25:32', type: 'warning', message: 'High CPU usage detected (85%)', severity: 'warning' },
    { time: '14:20:18', type: 'success', message: 'OSPF convergence completed', severity: 'success' },
    { time: '14:15:45', type: 'error', message: 'Interface eth2 link down', severity: 'error' },
    { time: '14:10:22', type: 'info', message: 'CloudPods resource sync completed', severity: 'info' },
  ],
  topRoutes: [
    { destination: '192.168.1.0/24', nextHop: '192.168.1.1', protocol: 'BGP', metric: 0, age: '2d 14h' },
    { destination: '10.0.0.0/8', nextHop: '10.0.0.1', protocol: 'OSPF', metric: 10, age: '2d 14h' },
    { destination: '172.16.0.0/12', nextHop: '172.16.0.1', protocol: 'BGP', metric: 0, age: '2d 14h' },
    { destination: '0.0.0.0/0', nextHop: '203.0.113.1', protocol: 'BGP', metric: 0, age: '2d 14h' },
  ],
};

const Dashboard: React.FC = () => {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [data, setData] = useState(mockData);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(5);

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        setError(null);
        
        // Simulate API calls
        await new Promise(resolve => setTimeout(resolve, 1000));
        
        // In a real implementation, these would be actual API calls
        // const systemStatus = await fetch('/api/v1/status').then(r => r.json());
        // const protocols = await fetch('/api/v1/protocols').then(r => r.json());
        // etc.
        
        setData(mockData);
        setLoading(false);
      } catch (err) {
        console.error('Failed to fetch dashboard data:', err);
        setError(err instanceof Error ? err.message : 'Unknown error');
        setLoading(false);
      }
    };

    fetchData();
    
    // Set up periodic refresh
    const interval = setInterval(fetchData, 30000); // Refresh every 30 seconds
    return () => clearInterval(interval);
  }, []);

  const handleRefresh = () => {
    setLoading(true);
    // Trigger refresh
    window.location.reload();
  };

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const getStatusColor = (status: string) => {
    switch (status.toLowerCase()) {
      case 'running':
      case 'up':
      case 'healthy':
      case 'connected':
        return 'success';
      case 'stopped':
      case 'down':
      case 'disconnected':
        return 'error';
      case 'warning':
        return 'warning';
      default:
        return 'default';
    }
  };

  const getSeverityIcon = (severity: string) => {
    switch (severity) {
      case 'success':
        return <CheckCircleIcon color="success" />;
      case 'warning':
        return <WarningIcon color="warning" />;
      case 'error':
        return <ErrorIcon color="error" />;
      default:
        return <InfoIcon color="info" />;
    }
  };

  if (loading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="400px">
        <LoadingSpinner size={60} />
      </Box>
    );
  }

  if (error) {
    return <ErrorMessage message={error} />;
  }

  return (
    <Box>
      {/* Header */}
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={3}>
        <Typography variant="h4" component="h1">
          Dashboard
        </Typography>
        <Box>
          <Tooltip title="Refresh">
            <IconButton onClick={handleRefresh} disabled={loading}>
              <RefreshIcon />
            </IconButton>
          </Tooltip>
          <Tooltip title="Settings">
            <IconButton>
              <SettingsIcon />
            </IconButton>
          </Tooltip>
        </Box>
      </Box>

      {/* System Status Overview */}
      <Grid container spacing={3} mb={3}>
        <Grid item xs={12} md={3}>
          <Card>
            <CardContent>
              <Box display="flex" alignItems="center" mb={2}>
                <RouterIcon color="primary" sx={{ mr: 1 }} />
                <Typography variant="h6">System Status</Typography>
              </Box>
              <StatusBadge 
                status={data.systemStatus.overall} 
                label={data.systemStatus.overall.toUpperCase()} 
              />
              <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                Uptime: {data.systemStatus.uptime}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Version: {data.systemStatus.version}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        
        <Grid item xs={12} md={3}>
          <Card>
            <CardContent>
              <Box display="flex" alignItems="center" mb={2}>
                <CloudIcon color="primary" sx={{ mr: 1 }} />
                <Typography variant="h6">CloudPods</Typography>
              </Box>
              <StatusBadge 
                status={data.cloudResources.cloudpods.status} 
                label={data.cloudResources.cloudpods.status.toUpperCase()} 
              />
              <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                Instances: {data.cloudResources.cloudpods.instances}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Networks: {data.cloudResources.cloudpods.networks}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        
        <Grid item xs={12} md={3}>
          <Card>
            <CardContent>
              <Box display="flex" alignItems="center" mb={2}>
                <SecurityIcon color="primary" sx={{ mr: 1 }} />
                <Typography variant="h6">Aviatrix</Typography>
              </Box>
              <StatusBadge 
                status={data.cloudResources.aviatrix.status} 
                label={data.cloudResources.aviatrix.status.toUpperCase()} 
              />
              <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                Gateways: {data.cloudResources.aviatrix.gateways}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Transit: {data.cloudResources.aviatrix.transitGateways}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        
        <Grid item xs={12} md={3}>
          <Card>
            <CardContent>
              <Box display="flex" alignItems="center" mb={2}>
                <SpeedIcon color="primary" sx={{ mr: 1 }} />
                <Typography variant="h6">Traffic</Typography>
              </Box>
              <Typography variant="h4" color="primary">
                {data.trafficStats.packetsPerSecond.toLocaleString()}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                packets/sec
              </Typography>
              <Typography variant="body2" color="text.secondary">
                {data.trafficStats.bytesPerSecond.toLocaleString()} bytes/sec
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {/* Performance Metrics */}
      <Grid container spacing={3} mb={3}>
        <Grid item xs={12} md={8}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Performance Metrics
              </Typography>
              <ResponsiveContainer width="100%" height={300}>
                <LineChart data={data.performanceMetrics}>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="time" />
                  <YAxis />
                  <RechartsTooltip />
                  <Line type="monotone" dataKey="cpu" stroke="#1976d2" strokeWidth={2} />
                  <Line type="monotone" dataKey="memory" stroke="#dc004e" strokeWidth={2} />
                  <Line type="monotone" dataKey="network" stroke="#2e7d32" strokeWidth={2} />
                </LineChart>
              </ResponsiveContainer>
            </CardContent>
          </Card>
        </Grid>
        
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Resource Usage
              </Typography>
              <Box mb={2}>
                <Box display="flex" justifyContent="space-between" mb={1}>
                  <Typography variant="body2">CPU</Typography>
                  <Typography variant="body2">65%</Typography>
                </Box>
                <LinearProgress variant="determinate" value={65} />
              </Box>
              <Box mb={2}>
                <Box display="flex" justifyContent="space-between" mb={1}>
                  <Typography variant="body2">Memory</Typography>
                  <Typography variant="body2">70%</Typography>
                </Box>
                <LinearProgress variant="determinate" value={70} />
              </Box>
              <Box mb={2}>
                <Box display="flex" justifyContent="space-between" mb={1}>
                  <Typography variant="body2">Network</Typography>
                  <Typography variant="body2">45%</Typography>
                </Box>
                <LinearProgress variant="determinate" value={45} />
              </Box>
              <Box mb={2}>
                <Box display="flex" justifyContent="space-between" mb={1}>
                  <Typography variant="body2">Storage</Typography>
                  <Typography variant="body2">30%</Typography>
                </Box>
                <LinearProgress variant="determinate" value={30} />
              </Box>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {/* Protocols and Interfaces */}
      <Grid container spacing={3} mb={3}>
        <Grid item xs={12} md={6}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Routing Protocols
              </Typography>
              <List>
                {data.protocols.map((protocol, index) => (
                  <React.Fragment key={index}>
                    <ListItem>
                      <ListItemIcon>
                        <NetworkCheckIcon color={getStatusColor(protocol.status) as any} />
                      </ListItemIcon>
                      <ListItemText
                        primary={protocol.name}
                        secondary={
                          <Box>
                            <Typography variant="body2" color="text.secondary">
                              Status: {protocol.status} | Neighbors: {protocol.neighbors} | Routes: {protocol.routes}
                            </Typography>
                            <Typography variant="body2" color="text.secondary">
                              Uptime: {protocol.uptime}
                            </Typography>
                          </Box>
                        }
                      />
                      <Chip 
                        label={protocol.status} 
                        color={getStatusColor(protocol.status) as any}
                        size="small"
                      />
                    </ListItem>
                    {index < data.protocols.length - 1 && <Divider />}
                  </React.Fragment>
                ))}
              </List>
            </CardContent>
          </Card>
        </Grid>
        
        <Grid item xs={12} md={6}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Network Interfaces
              </Typography>
              <List>
                {data.interfaces.map((iface, index) => (
                  <React.Fragment key={index}>
                    <ListItem>
                      <ListItemIcon>
                        <WifiIcon color={getStatusColor(iface.status) as any} />
                      </ListItemIcon>
                      <ListItemText
                        primary={iface.name}
                        secondary={
                          <Box>
                            <Typography variant="body2" color="text.secondary">
                              Speed: {iface.speed} | RX: {iface.rx.toLocaleString()} | TX: {iface.tx.toLocaleString()}
                            </Typography>
                            <Typography variant="body2" color="text.secondary">
                              Errors: {iface.errors}
                            </Typography>
                          </Box>
                        }
                      />
                      <Chip 
                        label={iface.status} 
                        color={getStatusColor(iface.status) as any}
                        size="small"
                      />
                    </ListItem>
                    {index < data.interfaces.length - 1 && <Divider />}
                  </React.Fragment>
                ))}
              </List>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {/* Recent Events and Top Routes */}
      <Grid container spacing={3}>
        <Grid item xs={12} md={6}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Recent Events
              </Typography>
              <List>
                {data.recentEvents.map((event, index) => (
                  <React.Fragment key={index}>
                    <ListItem>
                      <ListItemIcon>
                        {getSeverityIcon(event.severity)}
                      </ListItemIcon>
                      <ListItemText
                        primary={event.message}
                        secondary={event.time}
                      />
                    </ListItem>
                    {index < data.recentEvents.length - 1 && <Divider />}
                  </React.Fragment>
                ))}
              </List>
            </CardContent>
          </Card>
        </Grid>
        
        <Grid item xs={12} md={6}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Top Routes
              </Typography>
              <TableContainer>
                <Table size="small">
                  <TableHead>
                    <TableRow>
                      <TableCell>Destination</TableCell>
                      <TableCell>Next Hop</TableCell>
                      <TableCell>Protocol</TableCell>
                      <TableCell>Metric</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {data.topRoutes
                      .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                      .map((route, index) => (
                        <TableRow key={index}>
                          <TableCell>{route.destination}</TableCell>
                          <TableCell>{route.nextHop}</TableCell>
                          <TableCell>
                            <Chip 
                              label={route.protocol} 
                              size="small" 
                              color="primary"
                            />
                          </TableCell>
                          <TableCell>{route.metric}</TableCell>
                        </TableRow>
                      ))}
                  </TableBody>
                </Table>
              </TableContainer>
              <TablePagination
                rowsPerPageOptions={[5, 10, 25]}
                component="div"
                count={data.topRoutes.length}
                rowsPerPage={rowsPerPage}
                page={page}
                onPageChange={handleChangePage}
                onRowsPerPageChange={handleChangeRowsPerPage}
              />
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </Box>
  );
};

export default Dashboard;
