import React from 'react';
import { Chip } from '@mui/material';

interface StatusBadgeProps {
  status: string;
  label?: string;
  size?: 'small' | 'medium';
}

const StatusBadge: React.FC<StatusBadgeProps> = ({ 
  status, 
  label, 
  size = 'small' 
}) => {
  const getStatusColor = (status: string): 'default' | 'primary' | 'secondary' | 'error' | 'info' | 'success' | 'warning' => {
    switch (status.toLowerCase()) {
      case 'running':
      case 'up':
      case 'active':
      case 'healthy':
      case 'connected':
      case 'success':
        return 'success';
      case 'stopped':
      case 'down':
      case 'inactive':
      case 'disconnected':
      case 'error':
      case 'failed':
        return 'error';
      case 'pending':
      case 'warning':
      case 'standby':
        return 'warning';
      case 'info':
      case 'information':
        return 'info';
      default:
        return 'default';
    }
  };

  return (
    <Chip
      label={label || status}
      color={getStatusColor(status)}
      size={size}
      variant="filled"
    />
  );
};

export default StatusBadge;
