import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { Box, Container, AppBar, Toolbar, Typography, Button, IconButton, Drawer, List, ListItem, ListItemIcon, ListItemText, Divider, Badge, Alert, Snackbar } from '@mui/material';
import { 
  Dashboard as DashboardIcon, 
  Router as RouterIcon, 
  Cloud as CloudIcon, 
  Security as SecurityIcon, 
  Analytics as AnalyticsIcon, 
  Settings as SettingsIcon,
  Menu as MenuIcon,
  Notifications as NotificationsIcon,
  AccountCircle as AccountCircleIcon,
  NetworkCheck as NetworkCheckIcon,
  Speed as SpeedIcon,
  BugReport as BugReportIcon,
  Storage as StorageIcon
} from '@mui/icons-material';

// Import pages
import Dashboard from './pages/Dashboard';
import NetworkTopology from './pages/NetworkTopology';
import CloudPods from './pages/CloudPods';
import Aviatrix from './pages/Aviatrix';
import Analytics from './pages/Analytics';
import TrafficShaping from './pages/TrafficShaping';
import NetworkImpairments from './pages/NetworkImpairments';
import Testing from './pages/Testing';
import Settings from './pages/Settings';
import About from './pages/About';
import NotFound from './pages/NotFound';

// Import components
import Layout from './components/Layout';
import LoadingSpinner from './components/LoadingSpinner';
import ErrorBoundary from './components/ErrorBoundary';

// Create theme
const theme = createTheme({
  palette: {
    mode: 'dark',
    primary: {
      main: '#1976d2',
      light: '#42a5f5',
      dark: '#1565c0',
    },
    secondary: {
      main: '#dc004e',
      light: '#ff5983',
      dark: '#9a0036',
    },
    background: {
      default: '#0a0a0a',
      paper: '#1a1a1a',
    },
    text: {
      primary: '#ffffff',
      secondary: '#b0b0b0',
    },
  },
  typography: {
    fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
    h1: {
      fontSize: '2.5rem',
      fontWeight: 600,
    },
    h2: {
      fontSize: '2rem',
      fontWeight: 500,
    },
    h3: {
      fontSize: '1.75rem',
      fontWeight: 500,
    },
    h4: {
      fontSize: '1.5rem',
      fontWeight: 500,
    },
    h5: {
      fontSize: '1.25rem',
      fontWeight: 500,
    },
    h6: {
      fontSize: '1rem',
      fontWeight: 500,
    },
  },
  components: {
    MuiCard: {
      styleOverrides: {
        root: {
          backgroundColor: '#1a1a1a',
          border: '1px solid #333',
          borderRadius: '8px',
        },
      },
    },
    MuiButton: {
      styleOverrides: {
        root: {
          borderRadius: '6px',
          textTransform: 'none',
          fontWeight: 500,
        },
      },
    },
    MuiChip: {
      styleOverrides: {
        root: {
          borderRadius: '16px',
        },
      },
    },
  },
});

// Navigation items
const navigationItems = [
  { id: 'dashboard', label: 'Dashboard', icon: <DashboardIcon />, path: '/' },
  { id: 'topology', label: 'Network Topology', icon: <NetworkCheckIcon />, path: '/topology' },
  { id: 'cloudpods', label: 'CloudPods', icon: <CloudIcon />, path: '/cloudpods' },
  { id: 'aviatrix', label: 'Aviatrix', icon: <SecurityIcon />, path: '/aviatrix' },
  { id: 'traffic', label: 'Traffic Shaping', icon: <SpeedIcon />, path: '/traffic' },
  { id: 'impairments', label: 'Network Impairments', icon: <BugReportIcon />, path: '/impairments' },
  { id: 'analytics', label: 'Analytics', icon: <AnalyticsIcon />, path: '/analytics' },
  { id: 'testing', label: 'Testing', icon: <BugReportIcon />, path: '/testing' },
  { id: 'settings', label: 'Settings', icon: <SettingsIcon />, path: '/settings' },
];

interface AppState {
  loading: boolean;
  error: string | null;
  notifications: Array<{
    id: string;
    message: string;
    severity: 'success' | 'info' | 'warning' | 'error';
    timestamp: Date;
  }>;
  drawerOpen: boolean;
  currentPage: string;
}

const App: React.FC = () => {
  const [state, setState] = useState<AppState>({
    loading: true,
    error: null,
    notifications: [],
    drawerOpen: false,
    currentPage: 'dashboard',
  });

  // Initialize application
  useEffect(() => {
    const initializeApp = async () => {
      try {
        setState(prev => ({ ...prev, loading: true }));
        
        // Simulate initialization
        await new Promise(resolve => setTimeout(resolve, 1000));
        
        // Check if backend is available
        const response = await fetch('/api/v1/status');
        if (!response.ok) {
          throw new Error('Backend service unavailable');
        }
        
        setState(prev => ({ 
          ...prev, 
          loading: false, 
          error: null 
        }));
        
        // Add welcome notification
        addNotification('Router Simulator initialized successfully', 'success');
        
      } catch (error) {
        console.error('Failed to initialize app:', error);
        setState(prev => ({ 
          ...prev, 
          loading: false, 
          error: error instanceof Error ? error.message : 'Unknown error' 
        }));
        addNotification('Failed to initialize application', 'error');
      }
    };

    initializeApp();
  }, []);

  const addNotification = (message: string, severity: 'success' | 'info' | 'warning' | 'error') => {
    const notification = {
      id: Date.now().toString(),
      message,
      severity,
      timestamp: new Date(),
    };
    
    setState(prev => ({
      ...prev,
      notifications: [...prev.notifications, notification],
    }));
  };

  const removeNotification = (id: string) => {
    setState(prev => ({
      ...prev,
      notifications: prev.notifications.filter(n => n.id !== id),
    }));
  };

  const toggleDrawer = () => {
    setState(prev => ({ ...prev, drawerOpen: !prev.drawerOpen }));
  };

  const handlePageChange = (pageId: string) => {
    setState(prev => ({ ...prev, currentPage: pageId }));
  };

  if (state.loading) {
    return (
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          minHeight="100vh"
          bgcolor="background.default"
        >
          <LoadingSpinner size={60} />
        </Box>
      </ThemeProvider>
    );
  }

  if (state.error) {
    return (
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          minHeight="100vh"
          bgcolor="background.default"
          flexDirection="column"
          gap={2}
        >
          <Typography variant="h4" color="error" gutterBottom>
            Application Error
          </Typography>
          <Typography variant="body1" color="text.secondary" textAlign="center">
            {state.error}
          </Typography>
          <Button 
            variant="contained" 
            color="primary" 
            onClick={() => window.location.reload()}
            sx={{ mt: 2 }}
          >
            Retry
          </Button>
        </Box>
      </ThemeProvider>
    );
  }

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <ErrorBoundary>
        <Router>
          <Box sx={{ display: 'flex', minHeight: '100vh' }}>
            {/* App Bar */}
            <AppBar position="fixed" sx={{ zIndex: theme.zIndex.drawer + 1 }}>
              <Toolbar>
                <IconButton
                  color="inherit"
                  aria-label="open drawer"
                  onClick={toggleDrawer}
                  edge="start"
                  sx={{ mr: 2 }}
                >
                  <MenuIcon />
                </IconButton>
                <RouterIcon sx={{ mr: 2 }} />
                <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                  Router Simulator
                </Typography>
                <Badge badgeContent={state.notifications.length} color="secondary">
                  <IconButton color="inherit">
                    <NotificationsIcon />
                  </IconButton>
                </Badge>
                <IconButton color="inherit">
                  <AccountCircleIcon />
                </IconButton>
              </Toolbar>
            </AppBar>

            {/* Navigation Drawer */}
            <Drawer
              variant="temporary"
              open={state.drawerOpen}
              onClose={toggleDrawer}
              sx={{
                width: 280,
                flexShrink: 0,
                '& .MuiDrawer-paper': {
                  width: 280,
                  boxSizing: 'border-box',
                  backgroundColor: 'background.paper',
                  borderRight: '1px solid #333',
                },
              }}
            >
              <Toolbar />
              <Box sx={{ overflow: 'auto', pt: 2 }}>
                <List>
                  {navigationItems.map((item) => (
                    <ListItem
                      key={item.id}
                      button
                      onClick={() => {
                        handlePageChange(item.id);
                        toggleDrawer();
                      }}
                      selected={state.currentPage === item.id}
                      sx={{
                        '&.Mui-selected': {
                          backgroundColor: 'primary.main',
                          '&:hover': {
                            backgroundColor: 'primary.dark',
                          },
                        },
                      }}
                    >
                      <ListItemIcon sx={{ color: 'inherit' }}>
                        {item.icon}
                      </ListItemIcon>
                      <ListItemText primary={item.label} />
                    </ListItem>
                  ))}
                </List>
                <Divider sx={{ my: 2 }} />
                <List>
                  <ListItem button onClick={() => handlePageChange('about')}>
                    <ListItemIcon>
                      <StorageIcon />
                    </ListItemIcon>
                    <ListItemText primary="About" />
                  </ListItem>
                </List>
              </Box>
            </Drawer>

            {/* Main Content */}
            <Box
              component="main"
              sx={{
                flexGrow: 1,
                p: 3,
                width: { sm: `calc(100% - 280px)` },
                ml: { sm: 0 },
                mt: 8, // Account for AppBar height
              }}
            >
              <Container maxWidth="xl">
                <Routes>
                  <Route path="/" element={<Dashboard />} />
                  <Route path="/topology" element={<NetworkTopology />} />
                  <Route path="/cloudpods" element={<CloudPods />} />
                  <Route path="/aviatrix" element={<Aviatrix />} />
                  <Route path="/traffic" element={<TrafficShaping />} />
                  <Route path="/impairments" element={<NetworkImpairments />} />
                  <Route path="/analytics" element={<Analytics />} />
                  <Route path="/testing" element={<Testing />} />
                  <Route path="/settings" element={<Settings />} />
                  <Route path="/about" element={<About />} />
                  <Route path="*" element={<NotFound />} />
                </Routes>
              </Container>
            </Box>
          </Box>

          {/* Notifications */}
          <Snackbar
            open={state.notifications.length > 0}
            autoHideDuration={6000}
            onClose={() => removeNotification(state.notifications[0]?.id || '')}
            anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
          >
            <Alert
              onClose={() => removeNotification(state.notifications[0]?.id || '')}
              severity={state.notifications[0]?.severity || 'info'}
              sx={{ width: '100%' }}
            >
              {state.notifications[0]?.message}
            </Alert>
          </Snackbar>
        </Router>
      </ErrorBoundary>
    </ThemeProvider>
  );
};

export default App;
